import React, { Component } from 'react'
import { DatePicker, Input } from 'antd';
import searchlogo from '../../Image/svg/Search.svg'
import Nurtiontpncell from './Nurtion_TPN_cell'
import moment from 'moment'
import * as ApiProvider from '../../API/ApiProvider'

class Nurtiontpnorder extends Component {
    state = {
        onEdit: false,
        tpndata: [],

        PickerDate: moment(moment().format('YYYYMMDD'), 'YYYYMMDD').valueOf(),
        UDORDER0: []
    }
    componentDidMount() {
        this.getAPITPNData()
    }
    getAPITPNData() {
        const { caseinfo } = this.props
        const HID = caseinfo.HISID.toString().trim()
        const CID = caseinfo.CASEID.toString().trim()
        // console.log(HID, CID)
        ApiProvider.getStoredProcedureUDORDER0(HID, CID, "123")
            .then(data => {
                this.setState({
                    UDORDER0: data.data
                })
            })
            .catch(err => {
                console.log("Andy test getStoredProcedureUDORDER0 NG " + err)
            });
    }
    Edit_state() {
        this.setState(
            {
                onEdit: true
            }
        )
    }
    handleOk = e => {
        console.log(e);
        this.setState({
            onEdit: false,
        });
    };

    handleCancel = e => {
        console.log(e);
        this.setState({
            onEdit: false,
        });
    };

    render() {
        // console.log("123")
        const { caseinfo } = this.props
        const title = ["開始時間", "結束時間", "狀態", "處置名稱"]
        const titilecell = title.map(function (info, index) {
            return (
                <div key={index} style={{ padding: "0px 5px", display: "flex", alignItems: "center" }}>
                    <t4>{info}</t4>
                </div>
            );
        })
        const ListCellData = this.state.UDORDER0
        const filterData = ListCellData.filter((element, index, arr) => {
            const time = this.state.PickerDate
            const starttime = moment(moment(element.UDBGNDT, 'YYYY-MM-DD-HH.mm.ss.000000').format("YYYYMMDD"), "YYYYMMDD").valueOf()
            // const endtime = moment(element.UDENDDT, 'YYYY-MM-DD-HH.mm.ss.000000').valueOf()
            return starttime === time
        })
        const DataCellRender = filterData.map(function (element, index, arr) {
            return (
                <Nurtiontpncell key={index} data={element} caseinfo={caseinfo} ></Nurtiontpncell>
            );
        })
        console.log(ListCellData)
        return (
            <div>
                <t1>TPN order</t1>
                <div style={{ display: "grid", gridTemplateColumns: "220px 1fr 25px", gridColumnGap: "10px" }}>
                    <DatePicker defaultValue={moment(this.state.PickerDate)} allowClear={false} onChange={(date, datestring) => { this.setState({ PickerDate: date.valueOf() }) }} />
                    {/* <Input id="nurtion_feeding_input" placeholder="請輸入關鍵字" />
                    <div onClick={() => this.search_click()} style={{ display: "flex", alignItems: "center" }}>
                        <img src={searchlogo} alt="searchlogo" />
                    </div> */}
                </div>
                <div style={{ display: "grid", gridTemplateColumns: " 1fr 1fr 60px 450px", background: "#cccccc", height: "50px", marginTop: "10px" }}>
                    {titilecell}
                </div>
                <div style={{ minHeight: "120px", maxHeight: "400px", overflowY: "auto" }}>
                    {DataCellRender}
                </div>
            </div>
        );
    }
}

export default Nurtiontpnorder
