import React, { Component } from 'react';
import * as d3 from "d3";
import './Wardcardvitalsign.scss';
import { mmddformat } from '../Function/Timeformat'
import * as ApiProvider from '../API/ApiProvider'
import moment from 'moment';



class WardcardvitalsignChart extends Component {
    state = {
        sourcedata: []
    }
    componentDidUpdate() {
        const { svg_key } = this.props
        d3.select("#" + svg_key).remove()
        this.drawChart()

    }
    componentDidMount() {
        const { id_key } = this.props
        function selectType(type) {
            switch (type) {
                case "div_hr":
                    return "hr"
                case "div_bp":
                    return "nbpd"
                case "div_rr":
                    return "rr"
                case "div_spo":
                    return "spo2"
                case "div_abp":
                    return "abpd"
                case "div_bt":
                    return "bt"
                default:
                    break;
            }
        }
        // const simdata = vitalData.simdata
        const endTime = moment(moment().format("YYYYMMDDHH"), "YYYYMMDDHH").format("YYYYMMDDHHmmss");
        const startTime = moment(moment(moment().format("YYYYMMDDHH"), "YYYYMMDDHH").valueOf() - (24 * 60 * 60 * 1000)).format("YYYYMMDDHHmmss");
        console.log(endTime, startTime)
        const type = selectType(id_key)
        const interval = 24
        const { HISID } = this.props
        const hisid = HISID.toString().trim()
        ApiProvider.getPatientWardValue(endTime, hisid, startTime, interval, type)
            .then(data => {
                this.setState({
                    sourcedata: data.data
                })
            })
            .catch(err => {
                console.log(err);
            })

    }



    cal_svg_hight() {
        const { axisTop, axisBot } = this.props
        if (axisTop) {
            if (axisBot) {
                return 160
            }
            else {
                return 130
            }
        }
        else {
            if (axisBot) {
                return 100
            }
            else {
                return 90
            }
        }
    }

    cal_svg_transform() {
        const { axisTop, axisBot } = this.props
        console.log(axisTop)
        if (axisTop) {
            if (axisBot) {
                return 70
            }
            else {
                return 50
            }
        }
        else {
            if (axisBot) {
                return 5
            }
            else {
                return 5
            }
        }
    }

    drawChart() {
        function multiFormat(date) {
            if (formatHour(date) % 4 === 0) {
                return formatHour(date)
            }
            else return null
        }
        function multibotformat(date) {
            if (new Date(date).getHours() === 0) {
                return mmddformat(date)
            }
            else return null
        }
        const { svg_key, circlrcolor, axisTop, id_key, axisBot, MAX, MIN, Upper, Lowe } = this.props
        const width = window.screen.availWidth * 0.25, height = 70, transformmargin = this.cal_svg_transform(), margin = 20
        const borderwidth = width / 27
        var time_now = moment(moment().format("YYYYMMDDHH"), "YYYYMMDDHH").valueOf()

        var time_array = []
        var time_axis_array = []
        for (let index = 0; index < 25; index++) {
            time_array.push({ key: "time", x_axis_time: time_now - index * 60 * 60 * 1000 })
            time_axis_array.push(time_now - index * 60 * 60 * 1000)
        }
        var time_array_reverse = time_array.reverse()
        var time_axis_array_reverse = time_axis_array.reverse()
        var formatHour = d3.timeFormat("%H")

        var x = d3.scaleLinear()
            .domain([time_array_reverse[0].x_axis_time, time_array_reverse[24].x_axis_time])
            .range([0, width - 40]);


        //x和y的比例尺
        var y = d3.scaleLinear()
            .domain([MIN, MAX])
            .range([height, 0])



        var yAxis = d3.axisLeft(y)
            .tickValues([MIN, MAX])
            .tickFormat(n => (n))
            .tickSize(0, 0)
            .tickPadding(10);

        var xAxisBottom = d3.axisBottom(x)
            .tickValues(time_axis_array_reverse)
            .tickSize(0, 0)
            .tickPadding(height + 10)
            .tickFormat(function (d) { return multibotformat(d); });


        var xAxis = d3.axisTop(x)
            .tickValues(time_axis_array_reverse)
            .tickSize(0, 0)
            .tickPadding(margin)
            .tickFormat(function (d) { return multiFormat(d); });

        var svg = d3.select("#" + id_key)
            .append("svg")
            .attr("id", svg_key)
            .attr("viewBox", [0, 0, width, this.cal_svg_hight()])
            .append("g")
            .attr("transform", "translate(" + 30 + "," + transformmargin + ")");


        //y軸
        svg.append("g")
            .call(yAxis)
            .attr("class", "axis");


        if (axisTop) {
            svg.append("g")
                .call(xAxis)
                .attr("class", "axis");
        }

        if (axisBot) {
            svg.append("g")
                .call(xAxisBottom)
                .attr("class", "axis");
        }

        //上下虛線
        svg.append('line')
            .attr('x1', 0)
            .attr('y1', y(MAX))
            .attr('x2', width)
            .attr('y2', y(MAX))
            .style('stroke', '#E0E0E0')
            .style('stroke-width', 1)
            .style('stroke-dasharray', 5.5);

        svg.append('line')
            .attr('x1', 0)
            .attr('y1', y(MIN))
            .attr('x2', width)
            .attr('y2', y(MIN))
            .style('stroke', '#E0E0E0')
            .style('stroke-width', 1)
            .style('stroke-dasharray', 5.5);


        for (let index = 0; index < time_array_reverse.length; index++) {
            svg.append('line')
                .attr('x1', x(time_array_reverse[index].x_axis_time))
                .attr('y1', 0)
                .attr('x2', x(time_array_reverse[index].x_axis_time))
                .attr('y2', height)
                .style('stroke', '#E0E0E0')
                .style('stroke-width', 1)
                .style('stroke-dasharray', 5.5);
        }




        var sim_data = this.state.sourcedata
        for (let index = 0; index < sim_data.length; index++) {
            const element = sim_data[index];
            const Xtime = moment(element.Time, "YYYY-MM-DD HH:mm:ss").valueOf() + (30 * 60 * 1000)
            svg.append('line')
                .attr('x1', x(Xtime))
                .attr('y1', y(20))
                .attr('x2', x(Xtime))
                .attr('y2', y(120))
                .style('stroke', circlrcolor)
                .style('opacity', 0.5)
                // .style('stroke', 'red')
                .style('stroke-width', borderwidth)
        }
        for (let index = 0; index < sim_data.length; index++) {

            const element = sim_data[index];
            const eledata = element.Data
            const boxwidth = 10
            const data_sorted = eledata.sort(d3.ascending)
            const q1 = d3.quantile(data_sorted, .25)
            const q3 = d3.quantile(data_sorted, .75)
            // const interQuantileRange = q3 - q1
            const minbox = data_sorted[0]
            const maxbox = data_sorted[data_sorted.length - 1]
            //console.log(element.Time)
            const Xtime = moment(element.Time, "YYYY-MM-DD HH:mm:ss").valueOf() + (30 * 60 * 1000)
            if (eledata.length === 0) {
                continue
            }

            svg.append("line")
                .attr("x1", x(Xtime))
                .attr("x2", x(Xtime))
                .attr("y1", y(minbox))
                .attr("y2", y(maxbox))
                .attr("stroke", circlrcolor)
                .style('stroke-width', 1)

            svg.append("rect")
                .attr("x", x(Xtime) - boxwidth / 2)
                .attr("y", y(q3))
                .attr("height", (y(q1) - y(q3)))
                .attr("width", boxwidth)
                .attr("stroke", circlrcolor)
                .style("fill", "rgba(255,255,255,1)")
                .style('stroke-width', 1);

            svg
                .selectAll("toto")
                .data([minbox, maxbox])
                .enter()
                .append("line")
                .attr("x1", x(Xtime) - boxwidth / 2)
                .attr("x2", x(Xtime) + boxwidth / 2)
                .attr("y1", function (d) { return (y(d)) })
                .attr("y2", function (d) { return (y(d)) })
                .attr("stroke", circlrcolor)
                .style('stroke-width', 1);

        }

    }

    render() {
        const { id_key } = this.props
        return (
            <div id={id_key}></div>
        );
    }
}

export default WardcardvitalsignChart;