import React, { Component } from 'react';
import { Select, Modal } from 'antd';
import * as ApiGithub from '../../API/ApiGithub'
import * as RegularData from '../../Data/RegularData'
import moment from 'moment'
import Examchart from './Examchart'
import Examaxis from './Examaxis'

class Labview extends Component {
    state = {
        Selectus: this.props.SelectStatus,
        PickerDateRange: [moment(), moment()],
        visible: false,
        lineChart: {},
    }
    componentDidMount() {
        // ApiGithub.getExamReport()
        //     .then(data => {
        //         this.setState({
        //             TitleList: data.data
        //         })
        //     })
        //     .catch(err=>{
        //         console.log("GithubAPI NG" + err)
        //     })
        // this.setState({
        //     TitleList: RegularData.ExamReportJson
        // })
    }

    callbackDateFunction = (value) => {
        this.props.parentDateCallback(value);
    }
    handleChange = (value) => {
        this.callbackDateFunction(value)
        this.setState({
            Selectus: value
        })
    }
    titleSearch = (value) => {
        this.props.parentTitleCallback(value.target.value);
    }
    CellRender() {
        const { DataList } = this.props
        // console.log(DataList)
        if (DataList.length === 0) {
            return null
        }
        const DataListCell = DataList.map(function (element, index) {
            const cellRender = element.Data.map(function (info, index) {
                return (
                    <centerstyle key={index} style={{ flex: 1, border: "1px solid #cccccc", borderCollapse: 'collapse' }}>
                        <t3>{info}</t3>
                    </centerstyle>
                );
            })
            return (
                <div key={index} style={{ display: "grid", gridTemplateColumns: "110px 1fr", height: "50px" }}>
                    <centerstyle style={{ border: "1px solid #cccccc", borderCollapse: 'collapse' }}>
                        <t3>{element.Time}</t3>
                    </centerstyle>
                    <div style={{ display: "flex", gridTemplateColumns: "repeat(9,1fr)" }}>
                        {cellRender}
                    </div>
                </div >
            );
        })
        return DataListCell
    }

    fixLineChartData = (e) => {
        const { DataList } = this.props;
        const value = e.target.value;
        const number = e.target.getAttribute('index');

        let temp = {}
        DataList.map(data => temp[data.Time] = data.Data[number])
        let showDate = {}
        showDate[value] = temp
        this.setState({
            lineChart: showDate,
        }, () => { this.showModel() });
    }
    showModel = () => {
        this.setState({
            visible: true,
        });
    }
    handleOk = e => {
        this.setState({
            visible: false,
        });
    };

    handleCancel = e => {
        this.setState({
            visible: false,
        });
    };

    charttableview() {
        const mainKey = Object.keys(this.state.lineChart)[0];
        // const data = this.state.lineChart[mainKey]
        // const date = Object.keys(data)
        console.log(this.state.lineChart)
        console.log(mainKey)
        // console.log(data)

        const row_title = [
            { item: 0, title: ["WBC", "Hb", "PLT"] },
            { item: 1, title: ["Na", "K", "Ca(Free,Total)", "Mg", "P", "BUN", "Cr"] },
            { item: 2, title: ["AST", "ALT", "rGGT", "Alkp", "Alb"] },
            { item: 3, title: ["ck", "ck-MB", "BNP", "Troponin I"] },
            { item: 4, title: ["PT", "APiT", "INR"], string: ["PT", "APiT", "INR"] },
            { item: 5, title: ["PH", "pCO2", "pO2", "HCO3", "BE", "O2", "TCO2", "SaCO2"] },
            { item: 6, title: ["糖化血色素", "空腹血糖"] }
        ]
        const chart = row_title[0].title.map(
            (info, index) =>
                <div key={index} style={{ display: "grid", gridTemplateColumns: "140px 1fr" }}>
                    <div style={{ display: "flex", justifyContent: "center", alignItems: "center" }}>{info}</div>
                    <div>
                        <Examchart Time={this.state.PickerDateRange} max={200} min={0} id_key={"id" + index} svg_id={"svg" + index}></Examchart>
                    </div>
                </div>
        )
        return (
            <div style={{ margin: "20px 0px" }}>
                <div style={{ display: "grid", gridTemplateColumns: "140px 1fr" }}>
                    <div></div>
                    <div>
                        <Examaxis axisTop={true} Time={this.state.PickerDateRange} axisBot={false} max={200} min={0} id_key={"id_axis_top_exam"} svg_id={"svg_axis_top_exam"}></Examaxis>
                    </div>
                </div>
                {chart}
                <div style={{ display: "grid", gridTemplateColumns: "140px 1fr" }}>
                    <div></div>
                    <div>
                        <Examaxis axisTop={false} Time={this.state.PickerDateRange} axisBot={true} max={200} min={0} id_key={"id_axis_bot_exam"} svg_id={"svg_axis_bot_exam"}></Examaxis>
                    </div>
                </div>
            </div>
        );
    }

    render() {
        const { Option } = Select;
        const visible = this.state.visible
        const handleOk = this.handleOk
        const handleCancel = this.handleCancel
        const fixLineChartData = this.fixLineChartData
        const lineChart = Object.keys(this.state.lineChart)[0]
        const charttableview = this.charttableview()
        const LabDataList = RegularData.ExamReportJson()
        //0:CBC 1:電解質 2:肝功能 3:心臟 4:凝血 5:GAS 6:Glucose
        const SelectList = LabDataList.map(function (info, index) {
            // console.log(info.index)
            return (
                <Option key={info.index} value={info.index} >{info.select}</Option>
            );
        })
        const SelectTitlefilter = LabDataList.filter(function (element, index) {
            const selectdata = Select
            return element.index = selectdata
        })
        if (SelectTitlefilter.length === 0) {
            return null
        }
        const SelectTitle = SelectTitlefilter[this.state.Selectus].title
        const SelectTitleRender = SelectTitle.map(function (info, index) {
            return (
                <centerstyle style={{ flex: 1 }} key={index}>
                    <button style={{ backgroundColor: "transparent", border: "2px blue none" }} index={index} value={info} onClick={fixLineChartData}>{info}</button>
                    <Modal
                        title={lineChart}
                        visible={visible}
                        onOk={handleOk}
                        onCancel={handleCancel}
                        width={1200}
                        footer={null}>
                        {charttableview}
                    </Modal>
                </centerstyle >
            );
        })
        const ExamLab = this.CellRender()
        // console.log(lineChart)

        // console.log(ExamLab)
        return (
            <div>
                <t1>LAB(常規檢驗)</t1>
                <div style={{ display: "grid", gridTemplateColumns: "auto 1fr", marginTop: "10px", gridColumnGap: "5px" }}>
                    <t2 style={{ display: "flex", alignItems: "center" }}>檢驗類別:</t2>
                    <div style={{ display: "flex", alignItems: "center" }}>
                        <Select defaultValue={this.state.Selectus} style={{ width: "80%" }} onChange={this.handleChange}>
                            {SelectList}
                        </Select>
                    </div>
                </div>
                <div style={{ marginTop: "10px", display: "grid", gridTemplateColumns: "110px 1fr", height: "50px", backgroundColor: "#E2EDF3" }}>
                    <centerstyle>
                        <t2>檢驗項目</t2>
                    </centerstyle>
                    <div style={{ display: "flex", gridTemplateColumns: "repeat(9,1fr)", justifycontent: "space-between" }} onClick={this.titleSearch}>
                        {SelectTitleRender}
                    </div>
                </div>
                <div>{this.state.Title}</div>
                {ExamLab}
            </div >
        );
    }
}

export default Labview;
