import React, { Component } from 'react'
import './Chronic_weighttable.scss'
import moment from 'moment'

class Chronicweighttable extends Component {
    // 0:一天 1:三天 2:一週 3:兩週
    switch_state() {
        const { chronic_state } = this.props
        switch (chronic_state) {
            case 0:
                return 24
            case 1:
                return 21
            case 2:
                return 21
            case 3:
                return 14
            default:
                break;
        }
    }
    switch_state_title() {
        return "140px"
    }
    celltable() {
        function changegridnumber(key) {
            switch (key) {
                case 0:
                    return 24
                case 1:
                    return 18
                case 2:
                    return 21
                case 3:
                    return 14
                default:
                    break;
            }
        }
        const { chronic_state } = this.props
        const number = changegridnumber(chronic_state)
        var arr = []
        for (let index = 0; index < number; index++) {
            arr.push(
                <div className='cell'></div>
            )

        }
        return arr
    }
    render() {

        const { chronic_state, caseinfo, QTWPER, PickerDate } = this.props
        // const weight = caseinfo.WEIGHT
        // console.log(QTWPER,weight)
        const weight = [{
            "rec_dat": 20200204,
            "rec_tim": 1736,
            "ser_num": 9055917,
            "type": "TW",
            "value": 22,
            "value2": 800,
            "@xmlns:xsi": "http://www.w3.org/2001/XMLSchema-instance",
            "@xsi:type": "nnisRes"
        }, {
            "rec_dat": 20200205,
            "rec_tim": 1736,
            "ser_num": 9055917,
            "type": "TW",
            "value": 22,
            "value2": 1000,
            "@xmlns:xsi": "http://www.w3.org/2001/XMLSchema-instance",
            "@xsi:type": "nnisRes"
        }]
        function FormatEndTimeStamp(start, interval) {
            if (interval === 0) {
                return start - (24 * 60 * 60 * 1000)
            }
            if (interval === 1) {
                return start - (3 * 24 * 60 * 60 * 1000)

            }
            if (interval === 2) {
                return start - (7 * 24 * 60 * 60 * 1000)

            }
            if (interval === 3) {
                return start - (14 * 24 * 60 * 60 * 1000)

            }
        }
        const end = PickerDate + (31 * 60 * 60 * 1000)
        const start = FormatEndTimeStamp(end, chronic_state)
        const datafilter = weight.filter(function (value, index, arr) {
            const timeString = value.rec_dat.toString() + value.rec_tim.toString()
            const time = moment(timeString, "YYYYMMDDHHmm").valueOf()
            console.log(start, time, end)
            return start < time && time < end
        })
        const datafilterTime = datafilter.map(function (value, index, arr) {
            const timeString = value.rec_dat.toString() + value.rec_tim.toString()
            const time = moment(timeString, "YYYYMMDDHHmm").valueOf()
            return time
        })
        console.log(weight, datafilter, datafilterTime)
        const diff = end - start
        var widthArray = []
        for (let index = 0; index < datafilterTime.length; index++) {
            const element = datafilterTime[index];
            if (index === 0) {
                const width = parseInt(100 * (element - start) / diff)
                widthArray.push(width)
            }
            else if (index === (datafilterTime.length - 1)) {
                const width = parseInt(100 * (end - element) / diff)
                widthArray.push(width)
            }
            else {
                const width = parseInt(100 * ((datafilterTime[index + 1] - datafilterTime[index]) / diff) / diff)
                widthArray.push(width)
            }
        }
        console.log(widthArray)
        function changegridstyle(key) {
            switch (key) {
                case 0:
                    return 'chronicweighttableoneday'
                case 1:
                    return 'chronicweighttablethreeday'
                case 2:
                    return 'chronicweighttableoneweek'
                case 3:
                    return 'chronicweighttabletwoweek'
                default:
                    return null
            }
        }
        return (
            <div style={{ display: "grid", gridTemplateColumns: "140px 1fr", position: 'relative' }}>
                <div style={{ display: "flex", justifyContent: "center", alignItems: "center", background: "#cccccc" }}>
                    <t2>體重</t2>
                </div>
                <div className={changegridstyle(chronic_state)}>{this.celltable()}</div>
                <div style={{ position: 'absolute', left: 140 }}>
                    <div style={{ padding: "10px 0px", display: "flex", width: 'calc(100vw - 220px)', overflow: "hidden" }}>
                        <div style={{ width: '30%', height: "20", backgroundColor: "#3B97E1", color: "white", display: "flex", justifyContent: "center", alignItems: 'center' }}>1300g</div>

                    </div>
                </div>
            </div>
        );
    }
}

export default Chronicweighttable