import React, { Component } from 'react'
import { DatePicker, Select } from 'antd'
import Vitalxaxis from '../../Module/Vitalxaxis'
import Infectionvitalsign from './Infectionvitalsign'
import Infectionorder from './Infectionorder'
import Infectionlab from './Infectionlab'
import { statecolor } from '../../Function/Wardstate'
import { formatxArrayhalf, formatendtime, yyyymmddformat } from '../../Function/Timeformat'
import { simulationCentermonitordata, simulationnisdata } from '../../Function/Wardstate'
import Orderonchronic from '../ChronicleView/Chronic_order'
import moment from 'moment'
import * as ApiProvider from '../../API/ApiProvider'
import './Infection.scss'

class Infectionview extends Component {
    state = {
        datasource: [],
        statussource: "CM",
        intervalselect: 2,
        selectday: yyyymmddformat(new Date()),

        PickerDate: moment(moment().format("YYYYMMDD"), "YYYYMMDD").valueOf(),
        UDORDER0: [],
        RESLAB02: [],
        RESLAB01: []
    }
    componentDidMount() {
        this.apidata()
    }
    apidata() {
        const { caseinfo } = this.props
        const dateTime = this.state.PickerDate
        const et = dateTime + (31 * 60 * 60 * 1000)
        const st = moment(et - (7 * 24 * 60 * 60 * 1000)).format("YYYY-MM-DD")
        ApiProvider.getStoredProcedureUDORDER0(caseinfo.HISID, caseinfo.CASEID, "123")
            .then(data => {
                this.setState({
                    UDORDER0: data.data
                })
            })
            .catch(err => {
                console.log("Andy test getStoredProcedureORDLIST NG " + JSON.stringify(err))
            });
        ApiProvider.getStoredProcedureRESLAB02(caseinfo.HISID, st)
            .then(data => {
                this.setState({
                    RESLAB02: data.data
                })
            })
            .catch(err => {
                console.log("Andy test getQDC NG " + JSON.stringify(err));
            });
        ApiProvider.getStoredProcedureRESLAB01(caseinfo.HISID, st)
            .then(data => {
                this.setState({
                    RESLAB01: data.data
                })
            })
            .catch(err => {
                console.log("Andy test getQDC NG " + JSON.stringify(err));
            });
    }
    sourcehandleChange = (value) => {
        this.setState({
            statussource: value,
            datasource: this.switchdata(this.state.intervalselect, value)
        })

    }
    switchdata(value_interval, sourcefrom) {
        console.log(sourcefrom)
        // const { defaultdate } = this.props
        const defaultdate = new Date()
        function changevitaldays(vitaldays) {
            if (vitaldays === 3) {
                return 18
            }
            if (vitaldays === 7) {
                return 21
            }
            else {
                return 24
            }
        }

        function Interval_switch(interval) {
            if (interval === 0) {
                return 1
            }
            if (interval === 1) {
                return 3
            }

            if (interval === 2) {
                return 7
            }
        }
        const interval = Interval_switch(value_interval)
        const starttime = formatendtime(defaultdate) - (interval * 24 * 60 * 60 * 1000)
        if (sourcefrom === 'NIS') {
            const datatime = formatxArrayhalf(starttime, changevitaldays(interval))
            const data = simulationnisdata(datatime, changevitaldays(interval))
            return data
        }
        else {
            const datatime = formatxArrayhalf(starttime, changevitaldays(interval))
            const data = simulationCentermonitordata(datatime, changevitaldays(interval))
            return data
        }
    }
    onChange = (date, dateString) => {
        console.log(date, dateString);
        this.setState({
            selectday: yyyymmddformat(dateString),
            datasource: this.switchdata(this.state.intervalselect, this.state.statussource)
        })
    }
    render() {
        function vallofnull(value) {
            if (value === null || value === undefined || value === "") {
                return ""
            }
            return value.toString().trim()
        }
        const { Option } = Select;
        const { caseinfo } = this.props
        const LabData = this.state.RESLAB02
        const LabData2 = this.state.RESLAB01
        const dateTime = this.state.PickerDate
        const et = dateTime + (31 * 60 * 60 * 1000)
        const st = et - (7 * 24 * 60 * 60 * 1000)
        var WBCList = []
        var BandList = []
        var SegList = []
        var LymList = []
        var PCTList = []
        var CRPList = []
        if (Array.isArray(LabData)) {
            if (LabData.length !== 0) {
                for (let index = 0; index < LabData.length; index++) {
                    const element = LabData[index];
                    const WBC = vallofnull(element.RSV01)
                    const Band = vallofnull(element.RSV13)
                    const Seg = vallofnull(element.RSV14)
                    const Lym = vallofnull(element.RSV15)
                    const PCT = null
                    const time = moment((element.ORRESDT.toString() + element.ORRESTM.toString()), "YYYY-MM-DDHH.mm.ss").valueOf()
                    console.log(st, et, time)
                    if (st < time && time < et) {
                        if (WBC !== null || WBC !== "" || WBC !== undefined) {
                            WBCList.push({
                                Time: time,
                                Data: WBC
                            })
                        }
                        if (Band !== null || Band !== "" || Band !== undefined) {
                            BandList.push({
                                Time: time,
                                Data: Band
                            })
                        }
                        if (Seg !== null || Seg !== "" || Seg !== undefined) {
                            SegList.push({
                                Time: time,
                                Data: Seg
                            })
                        }
                        if (Lym !== null || Lym !== "" || Lym !== undefined) {
                            LymList.push({
                                Time: time,
                                Data: Lym
                            })
                        }
                        if (PCT !== null || PCT !== "" || PCT !== undefined) {
                            PCTList.push({
                                Time: time,
                                Data: PCT
                            })
                        }
                    }
                }
            }
        }
        if (Array.isArray(LabData2)) {
            if (LabData2.length !== 0) {
                for (let index = 0; index < LabData2.length; index++) {
                    const element = LabData2[index];
                    const CRP = vallofnull(element.RSV32)
                    const time = moment((element.ORRESDT.toString() + element.ORRESTM.toString()), "YYYY-MM-DDHH.mm.ss").valueOf()
                    if (st < time && time < et) {
                        if (CRP !== null || CRP !== "" || CRP !== undefined) {
                            CRPList.push({
                                Time: time,
                                Data: CRP
                            })
                        }
                    }
                }
            }
        }
        console.log(WBCList, BandList, SegList, LymList, PCTList, CRPList)
        return (
            <div style={{ padding: "0px 20px" }}>
                <div className='Infectioncontrol'>
                    <DatePicker placeholder="請選擇時間" />
                    <Select defaultValue={this.state.statussource} style={{ width: 200 }} onChange={this.sourcehandleChange}>
                        <Option value={'CM'}>Center Monitor</Option>
                        <Option value={'NIS'}>MIS</Option>
                    </Select>
                </div>
                <div style={{ margin: "10px 0px" }}>
                    <div style={{ display: "grid", gridTemplateColumns: "90px 1fr", height: "10px" }}>
                        <div></div>
                        <Vitalxaxis max={200} min={0} select_interval={2} select_date={new Date().getTime()} id_key="xaxis_infect" svg_id="xaxis_svg_infect"></Vitalxaxis>
                    </div>
                </div>
                <div className='boxshadow'></div>
                <div className='Infectionlayout'>
                    {/* <Infectionvitalsign statussource={this.state.statussource} datasource={this.state.datasource} select_date={this.state.selectday}></Infectionvitalsign> */}
                    {/* Order */}
                    <Orderonchronic PickerDate={this.state.PickerDate} UDORDER0={this.state.UDORDER0} select_interval={2} caseinfo={caseinfo} title=""></Orderonchronic>
                    <Infectionlab PickerDate={this.state.PickerDate} WBCList={WBCList} BandList={BandList} SegList={SegList} LymList={LymList} PCTList={PCTList} CRPList={CRPList}></Infectionlab>
                </div>
                <div style={{ display: "grid", gridTemplateColumns: "90px 1fr", height: "10px" }}>
                    <div></div>
                    <Vitalxaxis max={200} min={0} select_interval={2} select_date={new Date().getTime()} id_key="xaxis_flootsheet_bot" svg_id="xaxis_svg_flootsheet_bot"></Vitalxaxis>
                </div>
            </div>
        );
    }
}

export default Infectionview